* {
  font-family: sans-serif;
  font-size: 18px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 256px;
  text-align: center;
  border: 10px solid;
  background: black;
  border-radius: 5px;
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
}

input[type="text"] {
  height: 60px;
  width: 248px;
  background-color: black;
  border: 0px;
  color: white;
  text-align: right;
  font-size: 25px;
  font-weight: 900;
  letter-spacing: 1px;
}

#clear {
  grid-column: 1/3;
  grid-row: 1;
}

#equals {
  grid-column: 3/5;
  grid-row: 5;
}

button {
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

.highlight {
  background: skyblue;
}